// Base64 → Uint8Array（URLセーフに対応）
export function base64ToUint8Array(base64) {
    // パディングの補完
    let paddedBase = base64.replace(/-/g, '+').replace(/_/g, '/');
    const padding = paddedBase.length % 4;
    if (padding === 2)
        paddedBase += '==';
    else if (padding === 3)
        paddedBase += '=';
    const binaryString = atob(paddedBase);
    const len = binaryString.length;
    const bytes = new Uint8Array(len);
    for (let i = 0; i < len; i++) {
        bytes[i] = binaryString.charCodeAt(i);
    }
    return bytes;
}
// Uint8Array → Base64（URLセーフ対応）
export function uint8ArrayToBase64(bytes) {
    let binary = '';
    for (let i = 0; i < bytes.length; i++) {
        binary += String.fromCharCode(bytes[i]);
    }
    let base64 = btoa(binary);
    // URLセーフ変換
    base64 = base64.replace(/\+/g, '-').replace(/\//g, '_').replace(/=+$/, '');
    return base64;
}
// Decode => 展開 => YAML load
export async function decodeProcess(encodedData) {
    let dataObject = null;
    try {
        // デコード => 圧縮データ
        const compressedData = base64ToUint8Array(encodedData);
        // zstdで展開
        const yamlData = await zstdDecompress(compressedData);
        if (yamlData) {
            //console.debug(yamlData);
            // YAML文字列 => JavaScriptオブジェクト
            dataObject = loadYAML(yamlData);
        }
    }
    catch (error) {
        console.error("エラーが発生しました:", error);
    }
    return dataObject;
}
// YAML dump => 圧縮 => Encode
export async function encodeProcess(dataObject) {
    let encodedData = null;
    try {
        // YAMLオブジェクト => YAML文字列
        const yamlData = dumpYAML(dataObject);
        // zstdで圧縮
        const compressedData = await zstdCompress(yamlData);
        if (compressedData) {
            // 圧縮データ => 文字列
            encodedData = uint8ArrayToBase64(compressedData);
        }
    }
    catch (error) {
        console.error("エラーが発生しました:", error);
    }
    return encodedData;
}
